
import { defineComponent } from "vue";
export default defineComponent({
	name: "Profile",
	data() {
		return {
			user: {},
		};
	},
	created() {
		console.log(this);
		fetch("http://localhost:3500/api/v1/profile")
			.then((data) => data.json())
			.then((res) => (this.user = res));
	},
});
